import { useState, useEffect, useRef, useCallback } from "react";
import { useNavigate } from "react-router";

import HeaderInCourse from "./HeaderInCouse";
import alphavit_photo from '../../assets/img/covers/alphavit.jpg';
import course_one_photo from '../../assets/img/covers/curse-one.jpg';
import speech_photo from '../../assets/img/covers/speech.jpg';
import UmnichkaCourse from "./UmnichkaCourse";
import AlphavitCourse from "./AlphavitCourse";
import SpeechCourse from "./SpeechCourse";
import NewCourse from "./NewCourse";
// import './mainpage.scss';


const AllCourses = (props) => {
    const navigate = useNavigate();
    useEffect(()=>{
        urlAll.push(urlClick, urlAlphavit, urlSpeech, urlNew);
        countUrlLength(urlAll, url);
    }, []);
    const setContent = (process) => {
        switch (process) {
            case 'umnichka-course':
                return <UmnichkaCourse/>;
            case 'alphavit-course':
                return <AlphavitCourse/>;
            case 'speech-course':
                return <SpeechCourse/>;
            case 'new-course':
                return <NewCourse/>
            default:
                return (<h1>Упс...нет такой страницы...</h1>);
        }
    }

    const url = window.location.href;
    const urlClick = useRef(null);
    const urlAlphavit = useRef(null);
    const urlSpeech = useRef(null);
    const urlNew = useRef(null);
    let urlAll=[];

    
    const countUrlLength = (url, urlAll) => {
        if(url.length >24 ) {
            return findAndActivate(urlAll, url);
        } else {
            return null;
        }
    }
    const findAndActivate =(urlAll, url)=>{
        const exectly = urlAll.filter(item=> url.includes(`${item.current.dataset['name']}`) );
        return exectly[0].current.click();
    }

    const [content, setProcess] = useState();
    const ShowContent = content ? setContent(content) : null;

    return (
        <>  
            <div className="s-panels js-s-panels">
                <HeaderInCourse/>
                {/* <!-- preview panels -->
                <!-- ⚠️ don't remove the bg-opacity-0 className (toggled in JS) - the background color (bg-black) should be equal to the one of the .s-panels__project-figure elements --> */}
                <ul className="s-panels__projects-list js-s-panels__projects-list bg-black bg-opacity-0">
                    {/* Фото на заглавной Умничка 0+*/}
                    <li className="s-panels__project-preview js-s-panels__project-preview">
                        <div className="s-panels__project-intro">
                            <div className="s-panels__text-component text-component text-center padding-x-md color-white font-smooth text-shadow-lg">
                            <p className="color-inherit">Курс "Умничка 0+"</p>
                            <p className="opacity-60%">Гармоничное развитие ребенка от рождения до 1,5 лет</p>
                            </div>

                            <a ref={urlClick} onClick={()=>[setProcess(content => "umnichka-course"), navigate("/courses#umnichka-course")]} className="s-panels__project-control js-s-panels__project-control" data-name="#umnichka-course" href="#umnichka-course"
                            />
                        </div>

                        <figure className="s-panels__project-figure bg-black">
                            <img className="s-panels__project-img" src={alphavit_photo} alt="Image description"/>
                        </figure>

                        <div className="s-panels__project-title-wrapper padding-bottom-lg padding-top-xl">
                            <div className="container max-width-md">
                            <p className="s-panels__project-title text-center text-xxxl color-white text-shadow-md">Курс "Умничка 0+"</p>
                            </div>
                        </div>
                    </li>

                    {/*  Фото на заглавной ПРОЭКТ "Логоалфавит" */}
                    <li className="s-panels__project-preview js-s-panels__project-preview">
                    <div className="s-panels__project-intro">
                        <div className="s-panels__text-component text-component text-center padding-x-md color-white font-smooth text-shadow-lg">
                        <p className="color-inherit">Проэкт "Логоалфавит"</p>
                        <p className="opacity-60%">запуск речи и запоминание букв мягким естественным методом</p>
                        </div>

                        <a ref={urlAlphavit} onClick={()=>[setProcess(content => "alphavit-course"), navigate("/courses#alphavit-course")]} data-name="#alphavit-course" className="s-panels__project-control js-s-panels__project-control" href="#alphavit-course"></a>
                    </div>

                    <figure className="s-panels__project-figure bg-black">
                        <img className="s-panels__project-img" src={course_one_photo} alt="Image description"/>
                    </figure>

                    <div className="s-panels__project-title-wrapper padding-bottom-lg padding-top-xl">
                        <div className="container max-width-md">
                        <p className="s-panels__project-title text-center text-xxxl color-white text-shadow-md">Проэкт "Логоалфавит"</p>
                        </div>
                    </div>
                    </li>

                    {/* Фото на заглавной Гармоничное развития речи*/}
                    <li className="s-panels__project-preview js-s-panels__project-preview">
                    <div className="s-panels__project-intro">
                        <div className="s-panels__text-component text-component text-center padding-x-md color-white font-smooth text-shadow-lg">
                        <p className="color-inherit">Гармоничное развития речи</p>
                        <p className="opacity-60%">видео для малышей + большая программа по развитию, как базовому фундаменту для речи</p>
                        </div>

                        <a ref={urlSpeech} onClick={()=>[setProcess(content => "speech-course"), navigate("/courses#speech-course")]} data-name="#speech-course" className="s-panels__project-control js-s-panels__project-control" href="#speech-course"></a>
                    </div>

                    <figure className="s-panels__project-figure bg-black">
                        <img className="s-panels__project-img" src={speech_photo} alt="Image description"/>
                    </figure>

                    <div className="s-panels__project-title-wrapper padding-bottom-lg padding-top-xl">
                        <div className="container max-width-md">
                        <h1 className="s-panels__project-title text-center text-xxxl color-white text-shadow-md">Гармоничное развития речи</h1>
                        </div>
                    </div>
                    </li>

                    {/* Фото на заглавной Четвертый*/}
                    <li className="s-panels__project-preview js-s-panels__project-preview">
                    <div className="s-panels__project-intro">
                        <div className="s-panels__text-component text-component text-center padding-x-md color-white font-smooth text-shadow-lg">
                        <p className="color-inherit">Проект ХХХ</p>
                        <p className="opacity-60%">Супер ХХХ проект</p>
                        </div>

                        <a ref={urlNew} onClick={()=>[setProcess(content => "new-course"), navigate("/courses#new-course")]} className="s-panels__project-control js-s-panels__project-control" data-name="#new-course"  href="new-course"></a>
                    </div>

                    <figure className="s-panels__project-figure bg-black">
                        <img className="s-panels__project-img" src={speech_photo} alt="Image description"/>
                    </figure>

                    <div className="s-panels__project-title-wrapper padding-bottom-lg padding-top-xl">
                        <div className="container max-width-md">
                        <h1 className="s-panels__project-title text-center text-xxxl color-white text-shadow-md">Проект ХХХ</h1>
                        </div>
                    </div>
                    </li>
                </ul>

                {ShowContent}
            
                <div className="s-panels__overlay-layer bg-black js-s-panels__overlay-layer" aria-hidden="true"></div>
            </div>
        </>
    )
}

export default AllCourses;